import Link from 'next/link';
import { getProjectsWithPagination } from '@/lib/projects';
import { format } from 'date-fns';
import { enUS } from 'date-fns/locale';

export default function ProjectsPage() {
  // Para exportación estática, siempre usar página 1
  const currentPage = 1;
  const { projects, totalProjects, totalPages } = getProjectsWithPagination(currentPage, 8);

  return (
    <div className="min-h-screen" style={{ background: '#ffffff' }}>
      <div className="w-full mx-auto px-6 py-12">
        {/* Header */}
        <header className="mb-12">
          <h1 className="text-4xl font-bold mb-4" style={{ color: '#000000' }}>
          ;-- section.2.__TEXT.__projects:
          </h1>
          <p className="text-lg" style={{ color: '#666666' }}>
            A collection of creative and technical projects
          </p>
        </header>

        {/* Projects Grid */}
        <div className="grid gap-8 mb-12">
          {projects.map((project) => (
            <article 
              key={project.slug}
              className="border rounded-lg p-6 transition-all duration-200 hover:shadow-lg"
              style={{ 
                backgroundColor: '#f8f9fa', 
                borderColor: '#e1e5e9'
              }}
            >
              <header className="mb-4">
                <Link 
                  href={`/projects/${project.slug}`}
                  className="block group"
                >
                  <h2 
                    className="text-2xl font-bold mb-2 transition-colors group-hover:opacity-80"
                    style={{ color: '#000000' }}
                  >
                    {project.title}
                  </h2>
                </Link>
                <time 
                  className="text-sm"
                  style={{ color: '#666666' }}
                >
                  {format(new Date(project.date), 'dd MMMM yyyy', { locale: enUS })}
                </time>
              </header>
              
              {project.excerpt && (
                <p 
                  className="text-base mb-4 leading-relaxed"
                  style={{ color: '#333333' }}
                >
                  {project.excerpt}
                </p>
              )}
              
              <Link 
                href={`/projects/${project.slug}`}
                className="inline-flex items-center text-sm font-medium transition-colors"
                style={{ color: '#059669' }}
              >
                Read more
                <svg className="ml-2 h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                </svg>
              </Link>
            </article>
          ))}
        </div>

        {/* Pagination */}
        {totalPages > 1 && (
          <nav className="flex items-center justify-between pt-8" style={{ borderTop: '1px solid #e1e5e9' }}>
            <div className="flex items-center space-x-4">
              {currentPage > 1 && (
                <Link
                  href={`/projects?page=${currentPage - 1}`}
                  className="inline-flex items-center px-4 py-2 text-sm font-medium transition-colors rounded-md"
                  style={{ 
                    color: '#059669',
                    backgroundColor: '#f0fdf4',
                    border: '1px solid #bbf7d0'
                  }}
                >
                  <svg className="mr-2 h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                  </svg>
                  Previous
                </Link>
              )}
            </div>

            <div className="flex items-center space-x-2">
              {Array.from({ length: totalPages }, (_, i) => i + 1).map((pageNum) => (
                <Link
                  key={pageNum}
                  href={`/projects?page=${pageNum}`}
                  className={`inline-flex items-center px-3 py-2 text-sm font-medium rounded-md transition-colors ${
                    pageNum === currentPage
                      ? 'text-white'
                      : 'text-gray-700 hover:text-gray-900'
                  }`}
                  style={{
                    backgroundColor: pageNum === currentPage ? '#059669' : 'transparent',
                    border: pageNum === currentPage ? '1px solid #059669' : '1px solid #e1e5e9'
                  }}
                >
                  {pageNum}
                </Link>
              ))}
            </div>

            <div className="flex items-center space-x-4">
              {currentPage < totalPages && (
                <Link
                  href={`/projects?page=${currentPage + 1}`}
                  className="inline-flex items-center px-4 py-2 text-sm font-medium transition-colors rounded-md"
                  style={{ 
                    color: '#059669',
                    backgroundColor: '#f0fdf4',
                    border: '1px solid #bbf7d0'
                  }}
                >
                  Next
                  <svg className="ml-2 h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                  </svg>
                </Link>
              )}
            </div>
          </nav>
        )}

        {/* Project Count */}
        <div className="mt-8 text-center">
          <p className="text-sm" style={{ color: '#666666' }}>
            Showing {projects.length} of {totalProjects} projects
          </p>
        </div>

        {/* Footer */}
        <footer className="mt-24 pt-8" style={{ borderTop: '1px solid #e1e5e9' }}>
          <div className="flex flex-col md:flex-row justify-between items-start md:items-center">
            <p className="text-sm mb-4 md:mb-0" style={{ color: '#666666' }}>
              © 2004-{new Date().getFullYear()} shidare.com. Made with Next.js and Markdown.
            </p>
          </div>
        </footer>
      </div>
    </div>
  );
} 